// IIFE
(function(){
    document.addEventListener('DOMContentLoaded', function(){
        App(); 
    })
    
    function App(){
        Ventana();
        MedicionesDePantalla();
        navegacion();
        AccionesNavegacion();
     
    }

    //=======================================================================================
    // -> Funciones conectadas con otras funciones.
    
    function AccionesNavegacion(){
        const eliminar = document.querySelector('.ocultar');

        //contenedor que ya existe es nuestro DOM.
        const menu = document.querySelector('.phone-menu');


        if(eliminar){
            eliminar.classList.remove('ocultar');
        
            //cambiar los iconos
            let $script = `
                <picture>
                    <source width="200" height="300" srcset="build/img/close.png" alt="Cerrar nuestro menú">
                    <img id="Close-icon" width="200" height="300" src="build/img/close.webp" alt="Cerrar nuestro menú">
                </picture>
            `;
            menu.innerHTML = $script;
            
        } 

        const n = document.querySelector('picture #Close-icon');
        n.addEventListener('click', function(){
            ocultarNavegacion();
        })
           
    }

    function ocultarNavegacion(){
        const navegador = document.querySelector('#blogCafe_navegacion');
        navegador.classList.add('ocultar');

        const menu = document.querySelector('.phone-menu');

        //cambiar los iconos
        let $script = `
          <picture>
              <source width="200" height="300" srcset="build/img/menu.png" alt="Cerrar nuestro menú">
              <img id="Menu-icon" width="200" height="300" src="build/img/menu.webp" alt="Cerrar nuestro menú">
          </picture>
      `;
        menu.innerHTML = $script;

        //Nuevamente Repita para poder escuchar por los eventos.
        navegacion();
    }

    function MedicionesDePantalla(){
        let $size = document.documentElement.clientWidth;

        //icono para dispositivos menores de 768px.
        let $icono = document.querySelector('.phone-menu');

        //el navegador genérico.
        let $navegador = document.querySelector('#blogCafe_navegacion');

        //cuando la ventana sea grande.
        if($size >= 768 || innerWidth > 768){
            $icono.innerHTML = '';
            $icono.classList.add('ocultar');
            
            $navegador.classList.forEach(valor => {
                if(valor === 'ocultar'){
                    $navegador.classList.remove('ocultar');
                }
            })
            
        }else{
            //cuando la ventana sea no demasiada grande, ajecutará esta función.
            
            SmallWindows($icono, $navegador);
        }
    }

    function SmallWindows(icono, navegador){
        //Quitaremos la clase que oculta nuestro icono de menú, para dispositivos menores de 768px.
        icono.classList.remove('ocultar');
        navegador.classList.add('ocultar');
        AccionesNavegacion();
    }
    
    //===================================================================================
    // Registrar los eventos cuando le demos a nuestro Menú.
    //Un menú destinado para las medidas de pantallas de los celulares. de 
    function navegacion(){
        let iconoMenu = document.querySelector('#Menu-icon');
        iconoMenu.addEventListener('click', function(e){
            AccionesNavegacion();
        })
    }
    
    // eventos cuando cambiamos de tamaño a nuestra ventana.
    function Ventana(){ 
        window.addEventListener('resize', function(e){
            e.preventDefault();
            MedicionesDePantalla();
        });
    }

})();
